rl = `https://testingcf.jsdelivr.net/gh/mzrodyu/maomao/dist/_with_worldbook/ v${version}.json?_=${timestamp}`;\n    const jsonResponse = await fetch(jsonUrl);\n\n    if (!jsonResponse.ok) {\n      throw new Error(\n        currentLocale.value === 'zh-CN'\n          ? `: HTTP ${jsonResponse.status}`\n          : `Download failed: HTTP ${jsonResponse.status}`,\n      );\n    }\n\n    const jsonContent = await jsonResponse.text();\n\n    // 3. \n    const blob = new Blob([jsonContent], { type: 'application/json' });\n    const url = URL.createObjectURL(blob);\n    const a = document.createElement('a');\n    a.href = url;\n    a.download = ` v${version}.json`;\n    document.body.appendChild(a);\n    a.click();\n    document.body.removeChild(a);\n    URL.revokeObjectURL(url);\n\n    window.toastr.success(\n      currentLocale.value === 'zh-CN'\n        ? `v${version} `\n        : `v${version} downloaded successfully, please re-import the file in SillyTavern script library`,\n      currentLocale.value === 'zh-CN' ? '' : 'Success',\n      { timeOut: 8000 },\n    );\n  } catch (error: any) {\n    console.error(':', error);\n    window.toastr.error(\n      (currentLocale.value === 'zh-CN' ? ': ' : 'Download failed: ') + error.message,\n      currentLocale.value === 'zh-CN' ? '' : 'Error',\n      { timeOut: 5000 },\n    );\n  }\n};\n</script>\n\n<style scoped>\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n    transform: translateY(-10px);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0);\n  }\n}\n\n.update-list {\n  margin: 5px 0;\n  padding-left: 20px;\n  font-size: 14px;\n}\n\n.update-item {\n  margin-bottom: 8px;\n  line-height: 1.6;\n}\n</style>\n"],"sourceRoot":""}]);
